<script>
export default {
  data () {
    return {
        columns: [
          {
            title: 'ID',
            key: 'id',
            align: 'center'
          },
          {
            title: '名称',
            key: 'name',
            align: 'center'
          },
           {
            title: 'url',
            key: 'action',
            align: 'center',
            minWidth: 110,
            render: (h) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'info',
                    size: 'small'
                  },
                  style: {
                    margin: '0 4px 2px 0',
                  },
                  on: {
                    click: () =>{
                      window.open(this.path.pc_url, '_target')
                    }
                  },
                }, 'pc推送'),
                h('Button', {
                  props: {
                    type: 'warning',
                    size: 'small'
                  },
                  style: {
                    margin: '0 4px 2px 0'
                  },
                  on: {
                    click: () =>{
                      window.open(this.path.m_url, '_target')
                    }
                  },
                }, 'm推送'),
              ]);
            }
          }
        ],
        data: [
          {id:1,name:'百度推送'}
        ],
        path: {}
    }
  },
  created () {
    this.path = this.$store.state.otherListData
  }
}
</script>
<template>
  <div>
    <h3><Icon type="md-football" size=24 />url列表</h3>
    <Table  border :columns="columns" :data="data"></Table>
  </div>
</template>